import axios from 'axios'
import { open } from 'schema'
const xzLogin = function (notLogin) {
  if (!notLogin) {
    open.xz_open_login({
      success: () => {
        window.location.reload()
      }
    })
  }
}
const newAxios = (options) => {
  return new Promise((resolve, reject) => {
    axios(options)
      .then((res) => {
        if (open.isXZ || window.LEHE.isXZ_PAGE) {
          const { status, data: { code = 0, error_code: errorCode = 0, message = '' } } = res
          if (status === 401) {
            xzLogin(options.params.xzNotLogin)
            return reject(res.data)
          } else {
            if (Number(code) === 0 && Number(errorCode) === 0) {
              return resolve(res.data.data)
            } else if (Number(code) === 40001) {
              xzLogin(options.params.xzNotLogin)
              return reject(res.data)
            } else if (Number(errorCode) === 4133202 || Number(errorCode) === 4133203 || Number(errorCode) === 4040007) {
              xzLogin(options.params.xzNotLogin)
              return reject(res.data)
            } else if (Number(errorCode) === 4161107) {
              if (!options.params.xzNotLogin) {
                open.xz_open_login({
                  message: message
                })
              }
              return reject(res.data)
            } else if (Number(errorCode) === 4161220 || Number(errorCode) === 4161222) {
              open.xz_response_error_code({
                error_code: Number(errorCode),
                message: message
              })
              let data = {
                message: ''
              }
              return reject(data)
            } else {
              return reject(res.data)
            }
          }
        } else {
          const { status, data: { data, error_code: errorCode = -1, message, code = -1 } } = res
          if (errorCode > -1) {
            if (status !== 200 || errorCode !== 0) {
              return reject(new Error(message || ''))
            } else {
              return resolve(data || {})
            }
          }
          if (code > -1) {
            if (Number(code) === 40001) {
              open.login({
                success: function (data) {
                  window.location.reload()
                }
              })
              return reject(new Error(''))
            } else if (status !== 200 || Number(code) !== 0) {
              return reject(new Error(message || ''))
            } else {
              return resolve(data || {})
            }
          }
        }
      })
      .catch((err) => {
        return reject(err)
      })
  })
}

export default newAxios
