  @include hidden;
  @include pr;
  min-height: 2.00rem;
  text-align: left;
  .loading {
    @include h(.50rem);
    @include pt(.10rem);
    @include pb(.10rem);
    @include l(.30rem);
    @include left;
    @include w(100%);
    @include c;
    @include fc(#ccc);
    @include f(.22rem);
  }
  .more {
    width: 2.6rem;
    height: 2.6rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-right: .2rem;
    background-color: #fff;
    .more-plus {
      height: 0.76rem;
      line-height: 0.76rem;
      color: #333;
      font-size: 0.54rem;
    }
    .more-tip {
      margin: .14rem;
      height: .32rem;
      line-height: .32rem;
      font-size: .22rem;
      color: #999;
    }
  }

  .goods-box {
    @include left;
    @include w(100%);
    @include hidden;
    @include pr;
    padding:.20rem .30rem;
  }

  &.scroll {
    overflow: hidden;
    overflow-x: scroll;
