  display: none;
  width: 0;
  height: 0;
}
</style>

<template>
  <div class="gls-goods-list" :class="[data.type, 'type-' + data.align]" :style="styleObj.main">
    <goods-item ref="page" :goods-list="goodsList" :type="data.type" :user-style="styleObj">
      <div>
        <div class="more"
             :style="styleObj.item"
             v-if="data.type == 'scroll' && data.url"
             @click="jumpToUrl(data.url)">
          <div class="more-plus">
            +
          </div>

          <div class="more-tip">
            
          </div>
        </div>
      </div>
    </goods-item>
  </div>
</template>
<script>
import globalMixin from '../../../common/mixin/globalMixin'
import styleMixin from '../../../common/mixin/styleMixin'
import goodsItem from '../goodsitem/src/main'
import xzRequest from '../../../common/js/xzRequest'

export default {
  mixins: [globalMixin, styleMixin],
  name: 'XzGoodsList',
  components: {
    goodsItem: goodsItem
  },
  props: {
    data: Object
  },
  data () {
    return {
      goodsList: [],
      cursor: 0,
      wait: 0
    }
  },
  computed: {
    copyData: function () {
      let obj = JSON.parse(JSON.stringify(this.data))
      return obj
    }
  },
  watch: {
    'data': {
      async handler (newVal, oldVal) {
        if (JSON.stringify(newVal.goodsIdList) !== JSON.stringify(oldVal.goodsIdList)) {
          this.getGoodsList()
        } else if (JSON.stringify(newVal.goodsDataList) !== JSON.stringify(oldVal.goodsDataList)) {
          this.refreshData()
        }
      },
      deep: true
    }
  },
  methods: {
    async getGoodsList () {
      if (this.data.goodsIdList) {
        let mSelf = this
        if (this.data.goodsIdList.length > 0) {
          mSelf.goodsList = []
          let idsArray = []
          //  20 
          for (let i = 0; i < this.data.goodsIdList.length; i += 20) {
            idsArray.push(this.data.goodsIdList.slice(i, i + 20))
          }

          for (let j = 0; j < idsArray.length; j++) {
            let newIds = idsArray[j].join(',')
            await xzRequest({
              url: '/v/xz/api/v1/goods/get_goods_by_ids',
              params: {
                v: 0.3,
                pageSize: 20,
                goods_id: newIds,
                store_type: 1,
                account_id: LEHE.config.userInfo.account_id || ''
              },
              headers: {
                'Content-type': 'application/x-www-form-urlencoded'
              }
            }).then((res) => {
              if (res) {
                let requestDatas = JSON.parse(JSON.stringify(res))
                let orderedGoodsList = []
                this.data.goodsDataList.map((item) => {
                  for (let i = 0; i < requestDatas.length; i++) {
                    if (item.goods_id === requestDatas[i].goods_id) {
                      orderedGoodsList.push(Object.assign(requestDatas[i], item))
                      requestDatas.splice(i, 1)
                      break
                    }
                  }
                })
                mSelf.goodsList = mSelf.goodsList.concat(orderedGoodsList)
                setTimeout(function () {
                  mSelf.$emit('fetched')
                }, 500)
              }
            })
          }
        } else {
          mSelf.goodsList = []
        }
      }
    },
    refreshData () {
      let newGoodsList = []
      this.data.goodsDataList.map((item) => {
        for (let i = 0; i < this.goodsList.length; i++) {
          if (item.goods_id === this.goodsList[i].goods_id) {
            newGoodsList.push(Object.assign(this.goodsList[i], item))
            break
          }
        }
      })
      this.goodsList = JSON.parse(JSON.stringify(newGoodsList))
    }
  },
  mounted () {
    this.$emit('mounted')
    this.getGoodsList()
  }
}
</script>
