<!--VO+-->
<template>
  <div class="qiniu-video" :style="styleObj.main">
    <video
      id="video"
      class="video"
      ref="video"
      controls="controls"
      x5-video-player-type="h5-page"
      playsinline
      :autoplay="isAPP?data.autoplay:false"
      :loop="data.loop"
      :poster="data.poster?data.poster:poster"
      :preload="data.poster?'none':'auto'"></video>
  </div>
</template>

<script>
import styleMixin from '../../../common/mixin/styleMixin'
import globalMixin from '../../../common/mixin/globalMixin'
import visibilityMixin from '../../../common/mixin/documentVisibility.js'
import Hls from 'hls.js'
import ajax from '../../../common/js/request.js'
import { open } from 'schema'

export default {
  name: 'QiniuVideo',
  mixins: [styleMixin, globalMixin, visibilityMixin],
  components: {},
  props: {
    data: Object
  },
  data () {
    return {
      isAPP: true,
      poster: ''
    }
  },
  watch: {
    'data.video_path': {
      handler (newVal, oldVal) {
        if (newVal) {
          this.create(newVal)
        }
      },
      immediate: true
    }
  },
  methods: {
    create (path) {
      setTimeout(_ => {
        var video = this.$refs.video // document.getElementById('video')
        if (Hls.isSupported()) {
          var hls = new Hls()
          hls.loadSource(path)
          hls.attachMedia(video)
          hls.on(Hls.Events.MANIFEST_PARSED, function () {
            const promise = video.play()
            if (promise !== undefined) {
              promise
                .then(_ => {
                  video.play()
                  // Autoplay started!
                })
                .catch(error => {
                  console.log(error)
                  // Autoplay was prevented.
                  // Show a "Play" button so that user can start playback.
                })
            }
          })
        } else if (video.canPlayType('application/vnd.apple.mpegurl')) {
          video.src = path
          video.addEventListener('canplay', function () {
            video.play()
          })
        }
      })
    },
    visibilityShow () {
      if (this.$refs.video) {
        var playPromise = this.$refs.video.play()

        if (playPromise !== undefined) {
          playPromise.then(_ => {
            this.$refs.video.play()
          }).catch(err => {
            console.log(err)
          })
        }
      }
    },
    visibilityHidden () {
      this.$refs.video && this.$refs.video.pause()
    }
  },
  mounted () {
    this.isAPP = open.isAPP
    // if (navigator.userAgent.indexOf('UCBrowser') > -1) {
    ajax({
      url: '/video/get_video_by_persistent',
      params: {
        id: this.data.video_id,
        isMP4: true
      }
    })
      .then(res => {
        const video = this.$refs.video
        video.src = res.video_mp4_path
        this.poster = res.video_first_frame
        // const promise = video.play()
        // if (promise !== undefined) {
        //   promise.then(_ => {
        //     video.play()
        //     // Autoplay started!
        //   }).catch(error => {
        //     console.log(error)
        //     // Autoplay was prevented.
        //     // Show a "Play" button so that user can start playback.
        //   })
        // }
      })
      .catch(err => {
        console.log(err)
      })
    // } else {
    //   if (this.data.video_path === '') {
    //     ajax({
    //       url: '/video/get_video_by_persistent',
    //       params: {
    //         id: this.data.video_id,
    //         isMP4: true
    //       }
    //     }).then(res => {
    //       console.log(res)
    //       this.data.video_path = res.video_path
    //       this.create(res.video_path)
    //     })
    //       .catch(err => {
    //         console.log(err)
    //       })
    //   }
    // }
    this.$emit('mounted')
  }
}
</script>

