<template>
  <div class="gls-rich-text" :style="styleObj.main">
    <TextInline
      v-for="item in contentArray"
      :key="item._id"
      :attributes="item.attributes || {}"
      :content="item.insert" />
  </div>
</template>

<script>
import styleMixin from '../../../common/mixin/styleMixin'
import globalMixin from '../../../common/mixin/globalMixin'
import TextInline from './components/TextInline'

export default {
  name: 'RichText',
  components: {
    TextInline
  },
  mixins: [ globalMixin, styleMixin ],
  props: {
    data: Object
  },
  watch: {
    'data': {
      async handler (newVal, oldVal) {
        if (JSON.stringify(newVal.content) !== JSON.stringify(oldVal)) {
          if (newVal.content.ops) {
            newVal.content.ops.map((item) => {
              item['_id'] = Math.random()
            })
            this.contentArray = JSON.parse(JSON.stringify(newVal.content.ops))
          }
        }
      },
      deep: true
    }
  },
  data () {
    return {
      contentArray: []
    }
  },
  mounted () {
    this.contentArray = this.data.content.ops || []
    this.$emit('mounted')
  }
}
</script>

<style lang="scss" scoped>
.gls-rich-text {
  font-family: PingFang SC;
}
</style>
