<!---->
<template>
  <div class="hotmap-background" :style="styleObj.main">
    <img @load="imgLoad" class="hotmap-image" :src="data.hotmap.image" />
    <div
      v-for="(item,index) in data.hotmap.links"
      :key="index"
      :style="{
        top:px2rem(item.top),
        left:px2rem(item.left),
        height:px2rem(item.height),
        width:px2rem(item.width)
      }"
      class="hotmap-link"
      @click="openItem(item)" />
  </div>
</template>

<script>
import globalMixin from '../../../common/mixin/globalMixin'
import styleMixin from '../../../common/mixin/styleMixin'

export default {
  name: 'Hotmap',
  props: {
    data: Object
  },
  mixins: [ globalMixin, styleMixin ],
  methods: {
    px2rem (px) {
      return (px * 0.02 * 375 / 460) + 'rem'
    },
    imgLoad () {
      this.$emit('fetched')
    }
  },
  mounted () {
    this.$emit('mounted')
  }
}
</script>
<style scoped>
.hotmap-background{
  position: relative;
}
.hotmap-link{
  position: absolute;
}
.hotmap-image{
  width: 100%;
  display:block;
}
</style>
