<!---->
<template>
  <div class="gls-audio-normal" :class="[data.id, { 'gls-audio-normal-play': isPlay, 'gls-audio-normal-pause': !isPlay }]" :style="styleObj.main" @click="audioToggle" ref="normalAudio">
    <!-- ios wechat audio  -->
    <audio
      :src="data.audio"
      ref="audioToggle"
      loop="loop"
      class="audioToggle hh"
      :autoplay="data.autoplay" />
  </div>
</template>

<script>
import styleMixin from '../../../common/mixin/styleMixin'
import visibilityMixin from '../../../common/mixin/documentVisibility.js'

export default {
  name: 'NormalAudio',
  mixins: [ styleMixin, visibilityMixin ],
  props: {
    data: Object
  },
  data () {
    return {
      isPlay: false
    }
  },
  methods: {
    audioToggle: function () {
      var audioToggle = this.$refs.audioToggle
      this.isPlay = !this.isPlay
      if (this.isPlay) {
        audioToggle.play()
      } else {
        audioToggle.pause()
      }
    },
    autoPlayAudio: function () {
      var audioToggle = this.$refs.audioToggle
      if (window.wx) {
        wx.ready(function () {
          this.isPlay = true
          audioToggle.play()
        })
      } else {
        window.addEventListener('touchstart', function () {
          if (!this.isPlay) {
            this.isPlay = true
            audioToggle.play()
          }
        })
      }
    },
    visibilityShow () {
      this.$refs.audioToggle.play()
    },
    visibilityHidden () {
      this.$refs.audioToggle.pause()
    }
  },
  mounted: function () {
    this.$emit('mounted')
    if (this.$parent.$options.name === 'Middleware') {
      this.$refs.normalAudio.style.position = 'static'
    } else {
      var audioToggle = this.$refs.audioToggle

      // 
      audioToggle.volume = this.data.volume

      // 
      if (this.data.autoplay) {
        this.isPlay = true
        this.autoPlayAudio()
      }
    }
  }
}
</script>

<style lang="scss">
@import "../../../common/style/atom.scss";
.gls-audio-normal {
  @include pf;
  @include hidden;
  @include f(.24rem);
  @include w(.74rem);
  @include h(.74rem);
  margin: auto;
  right: .3rem;
  top:.3rem;
  z-index: 1001;
}
.gls-audio-normal-play {
  background: url(https://static.lehe.com/higo/feseTT/m/audio/glk_audio_play.png) no-repeat center center;
  background-size: cover;
  animation: playing 3s linear infinite;
  -webkit-animation: playing 3s linear infinite;
  -moz-animation: playing 3s linear infinite;
  -o-animation: playing 3s linear infinite;
}
.gls-audio-normal-pause {
  background: url(https://static.lehe.com/higo/feseTT/m/audio/glk_audio_pause.png) no-repeat center center;
  background-size: cover;
}

@keyframes playing {
  from {
    transform: rotate(0)
  }
  to {
    transform: rotate(360deg)
  }
}
@-webkit-keyframes playing {
  from {
    -webkit-transform: rotate(0)
  }
  to {
    -webkit-transform: rotate(360deg)
  }
}

@-moz-keyframes playing {
  from {
    -moz-transform: rotate(0)
  }
  to {
    -moz-transform: rotate(360deg)
  }
}
</style>
