<template>
  <div
    class="header"
    :style="styleObj.main">
    <div class="header-title">
      <div class="header-left">
        <div class="left-img-container">
          <img
            :src="dynamicInfo.avatar"
            class="header-left-img"
            @click="goHome(data.accountId)" />
        </div>
        <div class="header-left-content">
          <p
            class="title"
            v-text="dynamicInfo.nick_name"
            @click="goHome(data.accountId)" />
          <p
            class="tip-words"
            v-text="dynamicInfo.time_text" />
        </div>
      </div>
      <div class="header-right">
        <img
          v-if="data.showCollect && !isMe"
          :src="collectStatus ? 'https://pic.lehe.com/pic/_o/f3/d5/f6b61051a95d54a4552614a02c76_72_69.cz.png' : 'https://pic.lehe.com/pic/_o/42/0e/fc3d58073538aa484704b5f7dc10_72_72.cz.png'"
          class="btn-collect"
          @click="handleCollect" />
        <img
          v-if="data.showPrivate && !isMe"
          src="https://pic.lehe.com/pic/_o/6d/30/d39caba0da7743f4ebc39a738ee7_69_63.cz.png"
          class="btn-comment"
          @click="handleLetter" />
        <div
          class="btn-delete-wrapper"
          @click="handleMore">
          <img
            src="https://pic.lehe.com/pic/_o/88/b7/e2f5cbcdf50b48327936bdc40d2c_51_9.cz.png"
            class="btn-delete" />
        </div>
      </div>
    </div>
    <div
      v-if="data.showContent && dynamicInfo.content"
      class="header-text"
      v-text="dynamicInfo.content" />
    <transition name="fade">
      <div
        class="higo-toast"
        v-show="visible">
        {{ message }}
      </div>
    </transition>
  </div>
</template>

<script>
import { open } from 'schema'
import request from '../../../common/js/xzRequest.js'
import globalMixin from '../../../common/mixin/globalMixin.vue'
import styleMixin from '../../../common/mixin/styleMixin'

const followTypeList = {
  follow: '1',
  unsubscribe: '2'
}
export default {
  name: 'Attention',
  mixins: [styleMixin, globalMixin],
  props: {
    data: Object
  },
  mounted () {
    this.$emit('mounted')
    this.init()
  },
  destroyed () {
    clearTimeout(this.timer)
  },
  data () {
    return {
      dynamicInfo: {},
      timer: null,
      // 
      collectStatus: false,
      // 
      followFlag: true,
      // 
      letterFlag: true,
      visible: false,
      message: '',
      // accountId
      isMe: true
    }
  },
  watch: {
    'data.accountId': {
      handler (newVal, oldVal) {
        if (newVal) {
          this.init()
        }
      },
      deep: true
    }
  },
  methods: {
    getCookie (name) {
      let strcookie = document.cookie// cookie
      let arrcookie = strcookie.split('; ')// 
      // 
      for (let i = 0; i < arrcookie.length; i++) {
        let arr = arrcookie[i].split('=')
        if (arr[0] === name) {
          return arr[1]
        }
      }
      return ''
    },
    navigateTo (url, navigationHidden = 0) {
      // 
      const isFullUrl = /^http/.test(url)
      if (open.isXZ) {
        open['xz_open_webview']({
          url: isFullUrl ? url : `${window.location.origin}/xz${url}`,
          navigation_hidden: navigationHidden,
          back_button_hidden: 0
        })
      } else {
        if (process.env.NODE_ENV === 'development') {
          window.location.href = `${url}`
        } else {
          window.location.href = isFullUrl ? url : `/xz${url}`
        }
      }
    },
    async goHome (accountId) {
      try {
        await request({
          url: '/v/xz/api/v1/account/get_brief_by_token'
        })
        this.navigateTo(`/user/info/${accountId}`, 1)
      } catch (e) {
        this.showToast(e.message)
      }
    },
    showToast (message) {
      this.message = message
      this.visible = true
      this.timer = setTimeout(() => {
        this.visible = false
      }, 2500)
    },
    getTimeText (dateTimeStamp) {
      let result = ''
      let minute = 1000 * 60
      let hour = minute * 60
      let day = hour * 24
      let now = new Date().getTime()
      let diffValue = now - dateTimeStamp
      if (diffValue < 0) {
        return ''
      }
      let dayC = diffValue / day
      let hourC = diffValue / hour
      let minC = diffValue / minute
      if (dayC > 3) {
        const date = new Date(dateTimeStamp)
        result = `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`
      } else if (dayC >= 1 && dayC <= 3) {
        result = '' + parseInt(dayC) + ''
      } else if (hourC >= 1) {
        result = '' + parseInt(hourC) + ''
      } else if (minC >= 1) {
        result = '' + parseInt(minC) + ''
      } else {
        result = ''
      }
      return result
    },
    async getFollowStatus () {
      try {
        const followInfo = await request({
          url: '/v/xz/api/v1/account_relation/get_follow_status',
          params: {
            xzNotLogin: true,
            relation_account_id: this.data.accountId
          }
        })
        this.collectStatus = followInfo.follow_status === followTypeList.unsubscribe
      } catch (e) {
        console.log(e.message)
      }
    },
    async init () {
      try {
        const userInfo = await request({
          url: '/v/xz/api/v1/account/get_user_info',
          params: {
            account_ids: this.data.accountId
          }
        })
        this.dynamicInfo = {
          content: this.data.content,
          ...userInfo[0]
        }
        this.dynamicInfo.time_text = this.getTimeText(this.data.time)
        this.getFollowStatus()
        const mineInfo = await request({
          params: {
            xzNotLogin: true
          },
          url: '/v/xz/api/v1/account/get_brief_by_token'
        })
        this.isMe = mineInfo.account_id === this.dynamicInfo.account_id
      } catch (e) {
        console.log(e)
      }
    },
    // 
    async handleCollect () {
      if (this.followFlag) {
        this.followFlag = false
        try {
          await request({
            url: '/v/xz/api/v1/account_relation/handle_follow',
            method: 'post',
            params: {
              handle_account_id: this.data.accountId,
              type: this.collectStatus ? followTypeList.unsubscribe : followTypeList.follow
            }
          })
          this.showToast(`${this.collectStatus ? '' : ''}`)
          this.collectStatus = !this.collectStatus
        } catch (e) {
          this.showToast(e.message)
        }
        this.followFlag = true
      }
    },
    // 
    handleMore () {
      this.$emit('show-more', Object.assign({}, this.dynamicInfo, { isMe: this.isMe }))
    },
    // 
    async handleLetter () {
      if (this.letterFlag) {
        this.letterFlag = false
        setTimeout(() => {
          this.letterFlag = true
        }, 1000)
        try {
          await request({
            url: '/v/xz/api/v1/account/get_brief_by_token'
          })
          open.xz_say_greets({
            account_id: this.data.accountId
          })
        } catch (e) {
          this.showToast(e.message)
        }
      }
    }
  }
}
</script>

