<script>
export default {
  data () {
    return {
      form: {},
      styleData: {},
      obj: {}
    }
  },
  computed: {
    'styleObj': function () {
      var obj = {}
      for (let i in this.data.styleObj) {
        if (i.substring(i.length - 3) !== 'Obj') {
          obj[i] = {}
          for (let j in this.data.styleObj[i]) {
            var reg = /^(-)?[0-9]*$/
            if (reg.test(this.data.styleObj[i][j]) && j !== 'font-weight') {
              if (j === 'width' && this.data.styleObj[i][j] === 750) {
                obj[i][j] = '100%'
              } else {
                obj[i][j] = this.data.styleObj[i][j] * 0.01 + 'rem'
              }
            } else {
              obj[i][j] = this.data.styleObj[i][j]
            }
          }
        }
      }
      return obj
    }
  },
  watch: {
    'data.styleObj': {
      handler: function (val, oldVal) {
        //  styleObj={main : { height:'100',color:red }}
        //  {mainObj:{ height:'2rem',color:red },main : { height:'100',color:red }}
        for (let i in val) {
          if (i.substring(i.length - 3) !== 'Obj') {
            for (let j in val[i]) {
              var reg = /^[0-9]*$/
              if (reg.test(val[i][j]) && j !== 'font-weight') {
                this.obj[j] = val[i][j] * 0.01 + 'rem'
              } else {
                this.obj[j] = val[i][j]
              }
            }
            this.$set(this.data.styleObj, i + 'Obj', this.obj)
          }
        }
      },
      deep: true
    }
  }
}
</script>
