<template>
  <div class="gls-image-banner" :style="styleObj.main" :class="[data.type]">
    <swiper :options="swiperOption" ref="mSwiper">
      <swiper-slide v-for="item in dataCopy.images" :style="styleObj.images" :key="item.image_id">
        <div class="banner-title" v-if="item.link && item.link.bannerTitle">
          {{ item.link.bannerTitle }}
        </div>
        <div class="banner-desc" v-if="item.link && item.link.bannerDesc">
          {{ item.link.bannerDesc }}
        </div>
        <div class="core-image" :style="{'height': styleObj.main.height, 'background-image': 'url(' + item.image.image_middle + ')' }"></div>
      </swiper-slide>
      <div v-if="dataCopy.images.length > 1" class="swiper-pagination" slot="pagination" :style="styleObj.pager"></div>
    </swiper>
  </div>
</template>

<script>
import styleMixin from '../../../common/mixin/styleMixin'
import globalMixin from '../../../common/mixin/globalMixin'
import { swiper, swiperSlide } from '../../../common/components/swiper/index'

export default {
  name: 'ImageBanner',
  mixins: [ globalMixin, styleMixin ],
  components: {
    swiper,
    swiperSlide
  },
  props: {
    data: Object
  },
  computed: {
    dataCopy () {
      return JSON.parse(JSON.stringify(this.data))
    },
    swiperOption () {
      return this.getOption(this.data.type)
    }
  },
  data () {
    return {
      showSwiper: true
    }
  },
  watch: {
    dataCopy: function (newVal, oldVal) {
      this.$refs.mSwiper.update()
    }
  },
  methods: {
    open: function (index) {
      let image = this.dataCopy.images[index]
      this.openItem(image)
    },
    getOption: function (type) {
      let vm = this
      let imageLength = this.dataCopy.images.length || 1
      switch (this.data.type) {
        case 'banner2':
          return {
            spaceBetween: 30,
            effect: 'coverflow',
            slidesPerView: 750 / 350,
            centeredSlides: true,
            initialSlide: 1,
            autoplay: true,
            loop: imageLength > 1,
            coverflowEffect: {
              rotate: 0,
              stretch: -53,
              depth: 500,
              modifier: 1,
              slideShadows: false
            },
            pagination: imageLength > 1 ? {
              el: '.swiper-pagination',
              clickable: true
            } : {},
            on: {
              click: function () {
                vm.open(this.realIndex)
              }
            }
          }
        default:
          return {
            autoplay: true,
            loop: imageLength > 1,
            pagination: imageLength > 1 ? {
              el: '.swiper-pagination',
              clickable: true
            } : {},
            on: {
              click: function () {
                vm.open(this.realIndex)
              }
            }
          }
      }
    }
  },
  mounted () {
    this.$emit('mounted')
  }
}
</script>

