<script>
import open from '../js/open'

export default {
  methods: {
    jumpToUrl: function (url) {
      if (open.isXZ) {
        open.xz_url({
          url: url
        })
      } else {
        open.url({
          url: url
        })
      }
    },
    jumpToGoods: function (goodsId) {
      if (open.isXZ) {
        let params = encodeURIComponent(JSON.stringify({
          goods_id: goodsId
        }))
        open.xz_open_higo({
          scheme: `higo://goods_info.higo/?json_params=${params}`
        })
      } else {
        open.goods({
          goods_id: goodsId
        })
      }
    },
    jumpToGroup: function (groupId) {
      open.group({
        group_id: groupId
      })
    },
    openItem: function (item, isNormalImage = false, isPreview = false) {
      if (item.url && !item.link) {
        item.link = item.url
      }
      let { link: { type, value, valueField }, image = {} } = item
      let obj = {}
      if (type === 'qrcodeevent') {
        if (isNormalImage) {
          if (open.isHigo) {
            this.setOpen(type, obj)
          } else if (isPreview) {
            //  APP 
            open.previewImage(image.image_middle, window.ALLIMAGEURLS)
          }
        }
      } else if (type.indexOf('xz_banner_open') > -1) {
        // 
        let xzUrl = ''
        if (valueField === 'goods_id') {
          xzUrl = `https://m.lehe.com/xz/secret/detail/${value}`
        } else if (valueField === 'url') {
          xzUrl = value
        }
        if (open.isXZ) {
          open['xz_open_webview']({
            url: xzUrl,
            navigation_hidden: 0,
            back_button_hidden: 0
          })
        } else {
          window.location.href = xzUrl
        }
      } else if (type !== '' && type !== 'none' && value !== '') {
        if (valueField) {
          obj[valueField] = value
          obj['bi_data'] = window.LEHE.config.bi_data
        }
        this.setOpen(type, obj)
      }
    },
    setOpen: function (type, data) {
      if (open[type]) {
        open[type](data)
      }
    },
    login: function () {
      if (open.isXZ || LEHE.isXZ_PAGE) {
        open.xz_open_login({
          success: () => {
            window.location.reload()
          }
        })
      } else {
        open.login({
          success: function (data) {
            window.location.reload()
          }
        })
      }
    }
  }
}
</script>
