<template>
  <span
    @click="handleClick"
    :class="computedTextClass"
    :style="computedTextStyle"
    v-text="content">
  </span>
</template>

<script>
import globalMixin from '../../../../common/mixin/globalMixin'

export default {
  mixins: [ globalMixin ],
  props: {
    attributes: {
      type: Object,
      default: () => {}
    },
    content: {
      type: String,
      default: ''
    }
  },
  computed: {
    computedTextStyle () {
      let { bold, italic, underline, color, strike } = this.attributes
      let textStyle = {
        color: color
      }
      if (bold) {
        textStyle['fontWeight'] = 'bold'
      }
      if (italic) {
        textStyle['fontStyle'] = 'italic'
      }
      if (underline && strike) {
        textStyle['textDecoration'] = 'underline line-through'
      } else if (underline) {
        textStyle['textDecoration'] = 'underline'
      } else if (strike) {
        textStyle['textDecoration'] = 'line-through'
      }
      return textStyle
    },
    computedTextClass () {
      let { link } = this.attributes
      let textClass = {
        'text-inline-container': true
      }
      if (link) {
        textClass['link'] = true
      }
      return textClass
    }
  },
  methods: {
    handleClick () {
      const { link } = this.attributes
      if (link) {
        this.jumpToUrl(link)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.text-inline-container.link {
  color: #0000CC;
  text-decoration: underline;
}
.text-inline-container.link:hover {
  color: #0066CC;
}
</style>
