import HIGOAxios from './higoAxios'
// import qs from 'qs'
import sha from './sha'

export default function request (option) {
  const method = typeof option.method === 'string' ? option.method.toUpperCase() : 'GET'
  let data = option.params || option.data || {}
  // tokeninf
  // data.access_token = '18d4d9d3de366dc7e561d0ebf25aaf7b'

  // token
  // data.access_token = 'f1fa2d2e971cb4718bc9b42252d716a8'

  // token
  // data.access_token = 'cbac5c4cdc27e320ae612f91450a9050'

  data.app = 'xz'
  data.via = 'h5'
  data.s_secretKey = 'tBXg2CqRI2h3kR8r0krtS6vqlTBLpynn'
  data.s_st = new Date().getTime()
  let time = '' + data.s_st
  let arr = []
  let k
  for (k in data) {
    arr.push(k)
  }
  arr.sort()
  let arr2 = []
  for (let i = 0; i < arr.length; i++) {
    if (!!data[arr[i]] && data[arr[i]] !== 'undefined') {
      arr2.push(arr[i])
      arr2.push(data[arr[i]])
    } else {
      delete data[arr[i]]
    }
  }

  let str = arr2.join('')

  let sha1 = sha.hex_sha1(str)

  let start = Number(time.slice(time.length - 2, time.length - 1)) + Number(time.slice(time.length - 1, time.length))
  if (start >= 10) {
    start -= 10
  }
  data.s_sign = sha1.slice(start, start + 20)

  delete data.s_secretKey

  // let options = {
  //   headers: option.headers || {
  //     'Accept': 'application/json',
  //     'Content-Type': 'application/json'
  //     // 'Content-Type': (method !== 'GET') ? 'application/x-www-form-urlencoded' : 'application/json'
  //   },
  //   url: data.url || '/',
  //   baseURL: option.baseURL || window.LEHE_REQUEST_BASE ? window.LEHE_REQUEST_BASE + '/api' : 'https://m.lehe.com/api',
  //   timeout: 15000,
  //   method: method,
  //   params: data,
  //   // data: method !== 'GET' ? qs.stringify(data || {}) : {},
  //   withCredentials: true, // Cookies
  //   ...option
  // }
  let options = Object.assign({
    headers: option.headers || {
      'Accept': 'application/json',
      'Content-Type': 'application/json'
      // 'Content-Type': (method !== 'GET') ? 'application/x-www-form-urlencoded' : 'application/json'
    },
    url: data.url || '/',
    baseURL: data.baseURL || window.LEHE_REQUEST_BASE ? window.LEHE_REQUEST_BASE + '/api' : 'https://m.lehe.com/api',
    timeout: 15000,
    method: method,
    params: data,
    // data: method !== 'GET' ? qs.stringify(data || {}) : {},
    withCredentials: true // Cookies
  }, option)
  return HIGOAxios(options)
}
