import { HIGOWxSign } from 'schema'

export default function () {
  if (!window.ALLIMAGEURLS) {
    HIGOWxSign()
    window.ALLIMAGEURLS = []
    let { componentsTree: trees = [], componentsData: datas = [] } = window

    for (var i = 0; i < trees.length; i++) {
      if (trees[i].indexOf('normalPic') > -1) {
        if (datas[i]) {
          let componentItem = datas[i]
          let { images = [], isPreview } = componentItem
          if (isPreview) {
            images.map((imageItem) => {
              window.ALLIMAGEURLS.push(imageItem.image)
            })
          }
        }
      }
    }
  }
}
