<!---->
<template>
  <div class="gls-image-normal type-1row" :style="styleObj.main">
    <div class="image-box" :style="{...styleObj.image_box}" v-for="item in data.images" :key="item.image.image_id" @click="open(item)">
      <img :src="isShowOriginal ? item.image.image_original : item.image.image_middle" @load="imageLoaded" />
    </div>
  </div>
</template>

<script>
import setImageList from '../../../common/js/setImageList'
import styleMixin from '../../../common/mixin/styleMixin'
import globalMixin from '../../../common/mixin/globalMixin'
import { open } from 'schema'

export default {
  name: 'NormalPic',
  mixins: [ globalMixin, styleMixin ],
  props: {
    data: Object
  },
  data () {
    return {
      isShowOriginal: false
    }
  },
  methods: {
    imgPlaceholderHeight (original, image) {
      let newImgWidth
      let newImgHeight
      if (image.image_width <= 750 && image.image_height <= 4000) {
        newImgWidth = image.image_width
        newImgHeight = image.image_height
      } else if (image.image_width > 750 && image.image_height < 4000) {
        newImgWidth = 750
        newImgHeight = 750 * image.image_height / image.image_width
      } else if (image.image_width < 750 && image.image_height > 4000) {
        newImgWidth = 4000 * image.image_width / image.image_height
        newImgHeight = 4000
      } else {
        newImgWidth = 750
        newImgHeight = 4000
      }
      return window.innerWidth * newImgHeight / newImgWidth * 7.5 / window.innerWidth + 'rem'
    },
    imageLoaded () {
      this.$emit('fetched')
    },
    open (image) {
      setImageList()
      if (image.link.type === 'none' && this.data.isPreview) {
        open.previewImage(image.image.image_middle, window.ALLIMAGEURLS)
      } else {
        this.openItem(image, true, this.data.isPreview)
      }
    }
  },
  created () {
    this.isShowOriginal = /showoriginal/.test(window.location.href)
  },
  mounted () {
    this.$emit('mounted')
  }
}
</script>

<style lang="scss">
