<template>
  <div class="core-image" :style="imageStyle">
    <slot></slot>
  </div>
</template>

<script>

export default {
  name: 'CoreImage',
  props: {
    imagedata: {
      type: Object,
      default: function () {
        return {
          url: 'https://pic.lehe.com/pic/_o/2f/6d/7419a03b7cbbf810e8f0a2806dc2_750_500.cz.png'
        }
      }
    }
  },
  data () {
    return {
      loaded: false,
      imageSrc: 'https://pic.lehe.com/pic/_o/2f/6d/7419a03b7cbbf810e8f0a2806dc2_750_500.cz.png'
    }
  },
  computed: {
    imageStyle: function () {
      return {
        'background-image': 'url(' + this.imagedata.url + ')',
        'background-size': this.imagedata.size || 'cover'
      }
    }
  },
  mounted: function () {
    if (this.loaded) {
      return
    }
    var img = new Image()
    img.onload = () => {
      img.onload = null
      this.loaded = true
      this.imageSrc = this.imagedata.url
    }
    img.src = this.imagedata.url
  }
}
</script>

<style scoped rel="stylesheet/scss" lang="scss">
@import "../../common/style/atom.scss";
$higo_bg: "https://pic.lehe.com/pic/_o/2f/6d/7419a03b7cbbf810e8f0a2806dc2_750_500.cz.png";

.core-image{
  @include pr;
  @include hidden;
  background-color: #fff url($higo_bg) center no-repeat;
  background-size: contain;
  background-position: center;
}
</style>
