<template>
  <div>
    <div class="gls-timedown-normal" :style="styleObj.main">
      <span v-if="!isActive">{{ data.time.endText }}</span>
      <div class="timedown-inner" v-else>
        {{ data.time.activeText }}
        <span class="remains">{{ dateText }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import styleMixin from '../../../common/mixin/styleMixin'

export default {
  name: 'MixCountdown',
  mixins: [ styleMixin ],
  props: {
    data: Object
  },
  data () {
    return {
      endTime: null,
      remains: 0,
      dateText: '00:00:00',
      isActive: true,
      dateObj: {},
      oldDateObj: {},
      showh: true,
      showi: true,
      shows: true,
      timer: null
    }
  },
  computed: {
    copyData: function () {
      let obj = JSON.parse(JSON.stringify(this.data))
      return obj
    }
  },
  watch: {
    'data.time.end': function (newVal, oldVal) {
      this.reset()
    }
  },
  methods: {
    startTimeDown: function () {
      let self = this
      this.timer = setInterval(function () {
        if (self.remains < 0) {
          self.isActive = false
          self.dateText = self.dateObj.dateText = '00:00:00'
          clearInterval(self.timer)
        } else {
          self.remains = (self.endTime - new Date()) / 1000
          self.formatTimeText(self.remains)
          self.dateText = self.dateObj.dateText
        }
      }, 200)
    },
    initRemains: function () {
      this.isActive = true
      var now = new Date() / 1000
      this.endTime = new Date(this.data.time.end)
      var _remains = 0

      if (!this.endTime) {
        this.isActive = false
        return
      }

      _remains = this.endTime / 1000 - now
      if (_remains <= 0) {
        this.isActive = false
        _remains = 0
      }
      this.remains = _remains
    },

    formatTimeText: function (num) {
      let dateObj = {
        dateText: '00:00:00',
        day: '00',
        hour: '00',
        minute: '00',
        second: '00'
      }
      if (!isNaN(num) && num >= 0) {
        num = Math.floor(num)
        dateObj.second = String(num % 60).padStart(2, '0')
        if (num > 60) {
          num = Math.floor(num / 60)
          dateObj.minute = String(num % 60).padStart(2, '0')
          if (num > 60) {
            num = Math.floor(num / 60)
            dateObj.hour = String(num % 24).padStart(2, '0')
            if (num > 24) {
              num = Math.floor(num / 24)
              dateObj.day = String(num).padStart(2, '0')
            }
          }
        }
        if (dateObj.day !== '00') {
          dateObj.dateText = `${dateObj.day}  ${dateObj.hour}  ${dateObj.minute}  ${dateObj.second} `
        } else if (dateObj.hour !== '00') {
          dateObj.dateText = `${dateObj.hour}  ${dateObj.minute}  ${dateObj.second} `
        } else {
          dateObj.dateText = `${dateObj.minute}  ${dateObj.second} `
        }

        this.$set(this, 'dateObj', JSON.parse(JSON.stringify(dateObj)))
        this.$set(this, 'oldDateObj', JSON.parse(JSON.stringify(this.dateObj)))
      }
    },

    reset: function () {
      this.$set(this, 'dateObj', {})
      this.$set(this, 'oldDateObj', {})
      this.initRemains()
      this.startTimeDown()
    }
  },
  mounted () {
    this.$emit('mounted')
    this.reset()
  }
}
</script>

<style lang="scss">
@import "../../../common/style/atom.scss";
.gls-timedown-normal {
  @include hidden;
  @include pr;
  @include break-all;
  @include c;
  @include fc(#f66);
  font-family: avenir;
  font-family: PingFang SC;

  .timedown-inner {
    @include i_block;
    @include pl(.34rem);
    background: url('https://pic.lehe.com/pic/_o/34/b6/ad9d94feab09f7df51f1c53150dd_42_42.cz.png') no-repeat left center;
    background-size: .28rem;

    .remains {
      @include pl(.06rem);
    }
  }
}
</style>
