<template>
  <div class="gls-goods-poster-scroll goods-box" @click="openXzGoodsDetail(data.goods_id)">
    <div class="box" :style="userStyle.item">
      <core-image class="picture" :imagedata="{ url: data.goods_image || data.goodsPicUrl || data.main_image.image_middle }">
        <img class="image-icon" v-if="data.spotUrl" :src="data.spotUrl">
        <div class="advance-notice" v-if="data.advanceNoticeDate">
          {{ data.advanceNoticeBeginText }}{{ data.advanceNoticeDate }}{{ data.advanceNoticeEndText }}
        </div>
        <div class="sold-out" v-if="data.sold_out_type">
          <img
            :src="data.sold_out_type === 1 ? 'https://pic.lehe.com/pic/_o/42/dd/d33fddc9854c2965d8b0e8dd9b70_120_120.cz.png' : 'https://pic.lehe.com/pic/_o/fe/55/a898aa7e5f080ea430cf68fba6fc_122_120.cz.png'"
            alt="">
        </div>
      </core-image>

      <div class="content">
        <div class="series-type">
          {{ data.series_type_text }}
        </div>
        <div class="info">
          {{ data.goods_name || data.goodsName }}
        </div>
        <div class="price">
          <span class="original-price" :style="{ color: '#333' }">{{ data.goods_price ? '' + data.goods_price : ' ' }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import globalMixin from '../../../../../common/mixin/globalMixin'
import coreImage from '../../../../../common/components/coreImage'
import { open } from 'schema'
export default {
  props: {
    data: Object,
    index: Number,
    userStyle: Object
  },
  mixins: [globalMixin],
  components: {
    coreImage: coreImage
  },
  methods: {
    openXzGoodsDetail (goodsId) {
      if (open.isXZ) {
        open['xz_open_webview']({
          url: `https://m.lehe.com/xz/secret/detail/${goodsId}`,
          navigation_hidden: 0,
          back_button_hidden: 0
        })
      } else {
        window.location.href = `https://m.lehe.com/xz/secret/detail/${goodsId}`
      }
    }
  }
}
</script>

