import HIGOAxios from './higoAxios'
import promiseLimit from 'promise-limit'

// 
const CVER = '8.2.4'
// 
const limit = promiseLimit(4)

const request = (params) => {
  var options = {
    headers: params.headers || {
      // 'Accept': 'application/json'
    },
    url: params.url || '/',
    baseURL: params.baseURL || window.LEHE_REQUEST_BASE ? window.LEHE_REQUEST_BASE + '/api' : 'https://m.lehe.com/api',
    withCredentials: true, // Cookies
    method: params.method || 'get',
    params: params.params || {},
    data: params.data || {},
    config: params.config || {}
  }
  return HIGOAxios(options)
}

const higgsRequest = (option) => {
  if (!option.method || /get/ig.test(option.method)) {
    // 
    if (!option.params) {
      option.params = {
        cver: CVER
      }
    } else if (!option.params.cver) {
      option.params.cver = CVER
    }
  }
  return limit(() => request(option))
}

export default higgsRequest
