<template>
  <div class="gls-goods-poster-normal" @click="openXzGoodsDetail(data.goods_id)" :style="userStyle.item">
    <div class="img-wrap">
      <core-image :imagedata="{ url: data.goods_image || data.goodsPicUrl || data.main_image.image_middle }">
        <img class="image-icon" v-if="data.spotUrl" :src="data.spotUrl">
        <div class="advance-notice" v-if="data.advanceNoticeDate">
          {{ data.advanceNoticeBeginText }}{{ data.advanceNoticeDate }}{{ data.advanceNoticeEndText }}
        </div>
      </core-image>
    </div>

    <div class="desc">
      <div class="name">
        {{ data.brand_name }}
      </div>
      <div class="description">
        {{ data.goods_name || data.goodsName }}
      </div>
      <div class="price">
        <span class="span-1" :style="{ color: '#333' }">{{ data.goods_price ? '' + data.goods_price : ' ' }}</span>
        <!-- <span class="span-1" :style="{ color: data.inActivity && data.price && data.salePrice !== data.price ? '#f55' : '#333' }">{{ data.salePrice }}</span> -->
        <!-- <span class="origin-price" v-if="data.inActivity && data.price && data.salePrice !== data.price">{{ data.price }}</span> -->
        <!-- <span class="show-coupon" style="margin-left: .08rem;" v-show="data.availableCouponText && !data.inActivity">{{ data.availableCouponText }}</span> -->
      </div>
      <div class="sales-text">
        <span class="show-coupon" v-show="data.availableCouponText && data.inActivity">{{ data.availableCouponText }}</span>
        <span class="sold" v-if="data.salesVolumeDesc">{{ data.salesVolumeDesc }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import qs from 'qs'
import globalMixin from '../../../../../common/mixin/globalMixin'
import coreImage from '../../../../../common/components/coreImage'
import request from '../../../../../common/js/request'
import { open } from 'schema'

export default {
  props: {
    data: Object,
    userStyle: Object
  },
  components: {
    coreImage: coreImage
  },
  mixins: [globalMixin],
  methods: {
    wishList () {
      if (!LEHE.config.access_token) {
        this.login()
        return
      }
      let postData = {
        app: 'weapp',
        goods_id: this.data.goodsId,
        v: '0.2',
        ver: '0.8',
        access_token: LEHE.config.access_token
      }
      if (this.data.isWish === 1) {
        request({
          url: '/v/favorite/delete',
          method: 'post',
          data: qs.stringify(postData)
        }).then((res) => {
          this.$set(this.data, 'isWish', 0)
          this.$set(this.data, 'like', this.data.like - 1)
        })
      } else {
        request({
          url: '/v/favorite/create',
          method: 'post',
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          },
          data: qs.stringify(postData)
        }).then((res) => {
          this.$set(this.data, 'isWish', 1)
          this.$set(this.data, 'like', this.data.like + 1)
        })
      }
    },
    openXzGoodsDetail (goodsId) {
      if (open.isXZ) {
        open['xz_open_webview']({
          url: `https://m.lehe.com/xz/secret/detail/${goodsId}`,
          navigation_hidden: 0,
          back_button_hidden: 0
        })
      } else {
        window.location.href = `https://m.lehe.com/xz/secret/detail/${goodsId}`
      }
    }
  }
}

</script>
