<template>
  <div
    class="gls-bar-anchor"
    not_active="1"
    ref="anchor"
    :class="['type-' + dataCopy.type]"
    :style="styleObj.main">
    <div class="content-box">
      <ul>
        <li
          class="items" :style="styleObj.item"
          v-for="(item,index) in dataCopy.anchor.list"
          :key="index"
          :class="{ active:active=== index}"
          @click="selectActive(index)">
          <span class="anchor-text" v-if="dataCopy.type2 == &quot;normal&quot;" :style="active=== index ? styleObj.active : ''">{{ item.name }}</span>
          <img v-else :src="active=== index ? item.image2:item.image" class="anchor-img">
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import globalMixin from '../../../common/mixin/globalMixin'
import styleMixin from '../../../common/mixin/styleMixin'
let _lastTime = null

export default {
  name: 'Anchor',
  props: {
    data: Object,
    loadedList: {
      default () {
        return []
      },
      type: Array
    },
    fetchedList: {
      default () {
        return []
      },
      type: Array
    }
    // data: Object
  },
  data () {
    return {
      active: -1,
      isAllLoaded: false,
      monitored: []
    }
  },
  mixins: [globalMixin, styleMixin],
  computed: {
    dataCopy () {
      return JSON.parse(JSON.stringify(this.data))
    }
  },
  watch: {
    loadedList: {
      handler (val) {
        if (val.length === window.componentsTree.length) {
          this.isAllLoaded = true
          this.throttleAssignTreeArray()
        } else {
          this.isAllLoaded = false
        }
      },
      deep: true
    },
    fetchedList: {
      handler (val) {
        this.throttleAssignTreeArray()
      }
    }
  },
  mounted () {
    this.$emit('mounted')
    window.addEventListener('scroll', (e) => {
      if (!this.isAllLoaded) {
        return false
      }
      let documentOffsetHeight = document.documentElement.scrollTop + document.body.scrollTop
      this.monitored.forEach((value, index) => {
        if (documentOffsetHeight + this.$refs.anchor.offsetHeight >= value.from && documentOffsetHeight + this.$refs.anchor.offsetHeight < value.to) {
          this.active = value.index
        }
      })
    })
  },
  methods: {
    throttle (fn, gapTime) {
      return function () {
        let _nowTime = +new Date()
        if (_nowTime - _lastTime > gapTime || !_lastTime) {
          fn()
          _lastTime = _nowTime
        }
      }
    },
    throttleAssignTreeArray () {
      this.assignTreeArray()
      // this.throttle(this.assignTreeArray, 500)()
    },
    selectActive (index) {
      this.monitored.forEach(value => {
        if (value.index === index) {
          this.scrollToY(value.from - this.$refs.anchor.offsetHeight, 1500, 'easeInOutQuint')
        }
      })
    },
    assignTreeArray () {
      this.monitored = this.dataCopy.anchor.list.map((value, index) => {
        return {
          ...value,
          index,
          id: value.compId
        }
      })
      if (this.$parent.$options.name === 'Middleware') {
        this.$parent.$parent.$children.forEach(value => {
          this.monitored.forEach(item => {
            if (item.id === value.$attrs.id) {
              item.from = value.$el.parentNode.offsetTop
              item.to = item.from + value.$el.parentNode.offsetHeight
            }
          })
        })
      } else if (this.$parent.$options.name === 'App') {
        setTimeout(_ => {
          this.$nextTick(_ => {
          // 
            for (let i = this.monitored.length - 1; i >= 0; i--) {
              for (let j = 0; j < this.$parent.$children.length; j++) {
                if (this.monitored[i].id === this.$parent.$children[j].$attrs.id) {
                  if (i === this.monitored.length - 1) {
                    this.monitored[i].from = this.$parent.$children[j].$el.offsetTop
                    this.monitored[i].to = this.monitored[i].from + this.$parent.$children[j].$el.offsetHeight
                  } else {
                    this.monitored[i].from = this.$parent.$children[j].$el.offsetTop
                    this.monitored[i].to = this.monitored[i + 1].from
                  }
                }
              }
            }
          })
        })
      }
    },
    scrollToY (scrollTargetY = 0, speed = 2000, easing = 'easeOutSine') {
      // scrollTargetY: the target scrollY property of the window
      // speed: time in pixels per second
      // easing: easing equation to use

      let scrollY = document.documentElement.scrollTop + document.body.scrollTop
      let currentTime = 0

      // min time .1, max time .8 seconds
      var time = Math.max(0.1, Math.min(Math.abs(scrollY - scrollTargetY) / speed, 0.8))

      // easing equations from https://github.com/danro/easing-js/blob/master/easing.js
      var easingEquations = {
        easeOutSine: function (pos) {
          return Math.sin(pos * (Math.PI / 2))
        },
        easeInOutSine: function (pos) {
          return (-0.5 * (Math.cos(Math.PI * pos) - 1))
        },
        easeInOutQuint: function (pos) {
          if ((pos /= 0.5) < 1) {
            return 0.5 * Math.pow(pos, 5)
          }
          return 0.5 * (Math.pow((pos - 2), 5) + 2)
        }
      }

      // add animation loop
      function tick () {
        currentTime += 1 / 60

        var p = currentTime / time
        var t = easingEquations[easing](p)

        if (p < 1) {
          window.requestAnimationFrame(tick)
          // document.documentElement.scrollTop= scrollY + ((scrollTargetY - scrollY) * t)
          window.scrollTo(0, scrollY + ((scrollTargetY - scrollY) * t))
        } else {
          // document.documentElement.scrollTop(scrollTargetY)

          window.scrollTo(0, scrollTargetY)
        }
      }

      // call it once to get started
      tick()
    }
  }
}
</script>
