<!---->
<template>
  <div class="gls-text-label-container" @click="open()">
    <div class="gls-text-label" :style="styleObj.main" v-text="data.content" />
  </div>
</template>

<script>
import styleMixin from '../../../common/mixin/styleMixin'
import globalMixin from '../../../common/mixin/globalMixin'

export default {
  name: 'LabelText',
  mixins: [globalMixin, styleMixin],
  props: {
    data: Object
  },
  methods: {
    open () {
      let url = this.data.url
      // 
      if (url && url !== '') {
        this.jumpToUrl(url)
      } else {
        if (!this.data.link) {
          this.data.link = {}
        }
        this.openItem(this.data)
      }
    }
  },
  mounted () {
    this.$emit('mounted')
  }
}
</script>

<style scoped lang="scss">
@import "../../../common/style/atom.scss";
.gls-text-label-container {
  overflow:hidden;
  background-color:#fff;
  font-family: PingFang SC;
  .gls-text-label{
    font-family: "PingFang SC";
    @include hidden;
    @include pr;
    overflow-wrap: break-word;
    word-break: normal;
    white-space: pre-wrap;
    // ios
    -webkit-hyphens: auto;
    -ms-hyphens: auto;
    hyphens: auto;
  }
}
</style>
